import Layout from "@/components/layout/Layout";
import ProductImage from "@/components/common/ProductImage";
import AdvancedBreadcrumb from "@/components/common/Bredacrumb";
import FeaturePill from "@/components/common/FeaturePill";
import Link from "next/link";
import { notFound } from "next/navigation";

type Product = {
  id: string;
  title: string;
  description: string;
  slug: string;
  images: string[];
  features: string[];
  specifications: { title: string; value: string }[];
  category: {
    name: string;
    id: string;
    slug: string;
  };
};

async function getProductsByCategory(categoryId: string) {
  const res = await fetch(
    `https://app.realtimebiometrics.net/api/content/products?category_id=${categoryId}`,
    { cache: "no-store" }
  );

  if (!res.ok) throw new Error("Failed to fetch category products");

  const data = await res.json();
  return data?.data || [];
}

// ✅ Fixed metadata
export async function generateMetadata({ params }: { params: { id: string } }) {
  const { id } = params;
  const products = await getProductsByCategory(id);

  if (!products.length) {
    return {
      title: "Category Not Found",
      description: "No products found in this category.",
    };
  }

  const categoryName = products[0]?.category?.name || "Products";

  return {
    title: `${categoryName} | Realtime Biometrics`,
    description: `Explore our ${categoryName} range — high-quality biometric attendance and access control devices.`,
  };
}

// ✅ Fixed main page
export default async function CategoryPage({ params }: { params: { id: string } }) {
  const { id } = params;
  const products: Product[] = await getProductsByCategory(id);

  if (!products.length) notFound();

  const category = products[0]?.category;
  const breadcrumbItems = [
    { label: "Home", href: "/" },
    { label: "Products", href: "/products" },
    { label: category?.name || "Category", href: `/products/category/${id}` },
  ];

  return (
    <Layout>
      <AdvancedBreadcrumb items={breadcrumbItems} />
      <div className="max-w-6xl mx-auto px-6 py-12">
        <h1 className="text-3xl font-semibold text-gray-900 mb-8">
          {category?.name || "Products"}
        </h1>

        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-8">
          {products.map((product) => (
            <div
              key={product.id}
              className="bg-white border border-gray-200 rounded-2xl p-4 hover:shadow-lg transition duration-200"
            >
              <div className="aspect-[4/3] overflow-hidden rounded-xl mb-4">
                <ProductImage images={product.images || []} alt={product.title} />
              </div>

              <h2 className="text-xl font-medium text-gray-800 mb-2 line-clamp-1">
                {product.title}
              </h2>
              <p className="text-sm text-gray-500 line-clamp-2 mb-4">
                {product.description}
              </p>

              <div className="flex flex-wrap gap-2 mb-3">
                {product.features?.slice(0, 3)?.map((f, i) => (
                  <FeaturePill key={i} label={f} />
                ))}
              </div>

              <Link
                href={`/products/${product.slug}`}
                className="inline-block bg-[#EA5921] text-white text-sm px-4 py-2 rounded-md hover:bg-orange-600 transition"
              >
                View Details
              </Link>
            </div>
          ))}
        </div>
      </div>
    </Layout>
  );
}
